import React from "react";
import s from "../style/header.module.css";
const Header = () => {
  return (
    <div className={s.logo}>
      <img
        src={process.env.PUBLIC_URL + "/assets/setting.png"}
        alt="Your Image"
      />
      <p>Dashboard v.01</p>
    </div>
  );
};

export default Header;
